import React, { Component} from 'react';
import {BrowserRouter as Router, Link} from 'react-router-dom'
import { Collapse, Button, CardBody, Card } from 'reactstrap';
import  './Projects.css';
import Expandable from './Expandable'
import ProjectCard from './ProjectCard'
import ProjectCard2 from './ProjectCard2'
import pylogo from './img/logo_Python.png'
import blackjack1 from './img/blackjack_1.png'
import blackjack2 from './img/blackjack_2.png'
import browar1 from './img/browar_1.png'
import browar2 from './img/browar_2.png'
import ftp1 from './img/ftp_1.png'
import ftp2 from './img/ftp_2.png'
import ftp3 from './img/ftp_3.png'
import nfz1 from './img/nfz_1.png'
import nfz2 from './img/nfz_2.png'
import pct1 from './img/pct2075_1.png'
import pct2 from './img/pct2075_2.png'
import ships1 from './img/pyttleships_1.png'
import ships2 from './img/pyttleships_2.png'
import spectro1 from './img/spectro_1.png'
import vending1 from './img/vending_1.png'
import vending2 from './img/vending_2.png'
import Contact from './Contact';

import Header from './Header'


class Projects extends Component {
  constructor(props) {
    super(props);
    console.log(Contact.showing);
    this.state = {isOpen: false,
    }
    this.toggle = this.toggle.bind(this)
    this.pyttleships = {
        text_pl: ['Prosta aplikacja pozwalająca zagrać użytkownikowi w statki z komputerem.','Napisana w Pythonie z wykorzystaniem Kivy w celu stworzenia interfejsu graficznego.','Celem gry jest zestrzelenie wszystkich statków przeciwnika, zanim on zniszczy wszystkie statki gracza. Strzały oddawane są naprzemiennie aż do momentu trafienia w statek; w takiej sytuacji można oddać strzał ponownie. Zachowaniem przeciwnika steruje prosty algorytm pozwalający na losowe ustawienie statków, oddawanie strzałów w losowe miejsce na planszy, oraz w razie trafienia, ostrzelanie sąsiednich pól do momentu zatopienia trafionego statku.'],
        text_en: ['Simple application, that allows the user to play Battleships with a computer.','Written in Python, using Kivy in order to create graphical interface.','Main goal of the game is simple - shoot all of computer\'s ships before it destroys all of your ships. Players take turns taking shots until one of them hits; in this situation it is possible to take another shot. Computer\'s behavior is controlled by simple algorithm allowing random ship placement, random shots and, in case of hitting, shooting adjacent squares until hit battleship sinks.'],
        imgs: [ships1,ships2],
        github: 'https://github.com/hindhinge/Pyttleship',
        name:"ships"
      };

    this.blackjack = {
        text_pl: ['Aplikacja konsolowa pozwalająca na zagranie z komputerem w grę hazardową Blackjack.','Zachowanie krupiera zgodne jest ze strategią stosowaną w kasynach, czyli nie dobierać karty po osiągnięciu 17 punktów.'],
        text_en: ['Console application allowing player to play Blackjack.','Croupier\'s behavior responds to strategy used in casinos, that means, never to draw a card when you reach 17 points.'],
        imgs: [blackjack1,blackjack2],
        github: 'https://github.com/hindhinge/ConsoleBlackjack',
        name:"blackjack"
      };
    
    this.browar = {
      text_pl: ['Symulacja linii produkcyjnej w browarze za pomocą języka opisu sprzętu.','Projekt zaliczeniowy kursu wprowadzającego do VHDL.','Celem projektu było zasymulowanie wszystkich etapów produkcji butelki piwa (zakup butelek i kapsli, warzenie piwa, napełnianie i kapslowanie butelek), jako pojedynczego układu elektronicznego sterowanego za pomocą sygnałów zewnętrznych, odpowiadających sygnałom przekazywanym przez czujniki. W ramach projektu została wykonana jednostka testowa pozwalająca na zweryfikowanie poprawności działania symulacji.'],
      text_en: ['Simulation of a production line in a brewery, made in hardware description language.','Final project for introductory course to VHDL.','Project\'s main goal was to simulate all production stages for a bottle of beer (buying bottles and bottle caps, brewing beer, filling and capping bottles), as a single electronic system, driven by external signals corresponding to signals recieved from sensors. A testing unit, used to verify if all processes work correctly, was also written as a part of the project.',],
      imgs: [browar1,browar2],
      github: 'https://github.com/hindhinge/BROWAR',
      name:"browar"
      };
    this.pct = {
      text_pl: ['Projekt rzeczywistego układu scalonego PCT2075, napisany na bazie karty katalogowej elementu, zrealizowany z wykorzystaniem języka opisu sprzętu.','Urządzenie wykorzystuje maszynę stanów, sterującą zachowaniem układu, przez odpowiednią reakcję na sygnały doprowadzane przez jednostkę nadrzędną. Pozwala to na dwukierunkową komunikację za pomocą magistrali I2C, odczyt i konfigurację rejestrów wewnętrznych.'],
      text_en: ['Project simulating real-life integrated circuit PCT2075 thermometer, written in hardware description language, basing on a datasheet.','The device uses a state machine in order to react correctly to signals recieved from a master device. Allows for two-way communication using I2C bus, reading and writing to internal registries.'],
      imgs: [pct1,pct2],
      github: 'https://github.com/hindhinge/PCT2075',
      name:"pct"
      };
    this.ftp = {
      text_pl: ['Aplikacja mobilna napisana w Kotlinie.','Celem projektu było stworzenie aplikacji pozwalającej na połączenie się z wirtualną maszyną, z systemem z rodziny Unix, obecną na komputerze w sieci lokalnej. Po utworzeniu połączenia za pomocą protokołu SSH, na telefon pobierany jest plik tekstowy, zawierający zakodowaną informację o zdarzeniach związanych z klawiaturą, pobraną z folderu /dev/input/. Pobrany plik tekstowy jest następnie dekodowany, a na ekranie urządzenia wyświetlane są znaki wprowadzone za pomocą klawiatury.'],
      text_en: ['Mobile application written in Kotlin.','Project\'s goal was to create a mobile application, that allows to connect to a virtual machine launched on a computer within local network. After establishing a connection using SSH protocol, a .txt file, containing coded information about keyboard events, taken from /dev/input/ folder, is downloaded to phone\'s memory. The downloaded file is then decoded, and it\'s content is shown on device\'s screen.'],
      imgs: [ftp2,ftp3],
      github: 'https://github.com/hindhinge/FTPDisplayer',
      name:"ftp"
      };
    this.nfz = {
      text_pl: ['Aplikacja webowa symulująca panel administratora w przychodni lekarskiej.','Część aplikacji po strone serwera została wykonana w ASP.NET, natomiast część aplikacji po stronie klienta napisana została w React.','W skład aplikacji wchodzą następujące funkcje:','-Dodawanie, usuwanie oraz edycja pacjentów przychodni,','-Dodawanie, usuwanie oraz edycja lekarzy w przychodni,','-Ustalanie wizyt u poszczególnych lekarzy,','-Wysyłanie przypomnień pacjentom o wizytach za pomocą SMTP,','-Modyfikowanie bazy leków.'],
      text_en: ['A web application simulating administrator\'s panel in medical clinic.','Server-side application was written in ASP.NET, while client-side application was written in React.','Application allows you to:','-Add, remove and edit patients,','-Add, remove and edit doctors,','-Make an appointment with a selected doctor,','-Send reminders about visits through SMTP,','-Modify database containing available drugs.'],
      imgs: [nfz1,nfz2],
      github: 'https://github.com/hindhinge/NFZSim',
      name:"nfz"
      };
    this.spectro = {
      text_pl: ['Program będący tematem realizowanej przeze mnie pracy dyplomowej.','Aplikacja pozwalająca na analizę częstotliwościową sygnałów zapisanych w postaci plików dźwiękowych, lub na bieżąco, z mikrofonu podłączonego do komputera.','Wyniki analizy przedstawiane są następnie w postaci kolorowego, animowanego spektrogramu.'],
      text_en: ['An application being a topic of my dissertation.','The application allows user to analyse signals in frequency domain. Signals can be loaded from audio files on hard drive or recorded in real-time via microphone.','Analysis results are shown in from of colorful, animated spectrogram.'],
      imgs: [spectro1],
      github: '',
      name:"spectro"
      };
    this.vending = {
      text_pl: ['Projekt zaliczeniowy kursu Weryfikacja Systemów Cyfrowych.','Celem projektu było przygotowanie jednostki testowej dla projektu układu dostarczonego przez prowadzącego kurs. Wybraną jednostką testowaną w tym przypadku był automat do sprzedaży napojów.','Jednostka testowa pozwala na zasymulowanie procesu zakupu dowolnego napoju przez użytkownika. W trakcie symulacji kontrolowany jest stan rejestrów wewnętrznych automatu, a informacje o błędach zapisywane są do pliku tekstowego.'],
      text_en: ['Final project for Digital System Verification course.','Project\'s goal was to create a testing unit for a device designed by a course instructor. In this case testing unit was written for a vending machine.','The testing unit simulates a process of buying a beverage by a user. State of tested unit\'s internal registries is monitored during the simulation, while error messages are written to text file.'],
      imgs: [vending1,vending2],
      github: 'https://github.com/hindhinge/VendingMachineTB',
      name:"vending"
      };
    this.recent = {
      text_pl: '•Ostatnie',
      text_en: '•Recent'
    }
    this.older = {
      text_pl: '•Starsze',
      text_en: '•Older'
    }
    this.current = {
      text_pl: '•W trakcie',
      text_en: '•Current'
    }
};

componentDidMount() {
  Header.current = 'projects';
  console.log(Header.current);
}

toggle(){
    this.setState({isOpen: !this.state.isOpen})
}




render() {
return (
    <div className = "body">
      <p className="projects_categories">{this.props.language=='en'?this.recent.text_en:this.recent.text_pl}</p>
      <Expandable language={"Python"} name={"Pyttleships"}>
          <ProjectCard text={this.props.language=='en'?this.pyttleships.text_en:this.pyttleships.text_pl} imgs={this.pyttleships.imgs} github={this.pyttleships.github} name={this.pyttleships.name}/>
      </Expandable>
      <Expandable language={"Kotlin"} name={"FTPDisplayer"}>
          <ProjectCard text={this.props.language=='en'?this.ftp.text_en:this.ftp.text_pl} imgs={this.ftp.imgs} github={this.ftp.github} name={this.ftp.name}/>
      </Expandable>
      <Expandable language={"Asp"} name={"NFZSim"}>
          <ProjectCard text={this.props.language=='en'?this.nfz.text_en:this.nfz.text_pl} imgs={this.nfz.imgs} github={this.nfz.github} name={this.nfz.name}/>
      </Expandable>
      <Expandable language={"Vhdl"} name={"PTC2075"}>
          <ProjectCard text={this.props.language=='en'?this.pct.text_en:this.pct.text_pl} imgs={this.pct.imgs} github={this.pct.github} name={this.pct.name}/>
      </Expandable>
      <p className="projects_categories">{this.props.language=='en'?this.older.text_en:this.older.text_pl}</p>
      <Expandable language={"Verilog"} name={"VendingMachineTB"}>
          <ProjectCard text={this.props.language=='en'?this.vending.text_en:this.vending.text_pl} imgs={this.vending.imgs} github={this.vending.github} name={this.vending.name}/>
      </Expandable>
      <Expandable language={"Vhdl"} name={"BROWAR"}>
          <ProjectCard text={this.props.language=='en'?this.browar.text_en:this.browar.text_pl} imgs={this.browar.imgs} github={this.browar.github} name={this.browar.name}/>
      </Expandable>
      <Expandable language={"Cpp"} name={"ConsoleBlackjack"}>
          <ProjectCard text={this.props.language=='en'?this.blackjack.text_en:this.blackjack.text_pl} imgs={this.blackjack.imgs} github={this.blackjack.github} name={this.blackjack.name}/>
      </Expandable>
      <p className="projects_categories">{this.props.language=='en'?this.current.text_en:this.current.text_pl}</p>
      <Expandable language={"Python"} name={"Spectro"}>
          <ProjectCard text={this.props.language=='en'?this.spectro.text_en:this.spectro.text_pl} imgs={this.spectro.imgs} github="" name={this.spectro.name}/>
      </Expandable>
    </div>
);
}
}
 
export default Projects;